import React from 'react';
import Carousel from 'react-material-ui-carousel'
import { Box } from '@mui/material'

function ImageCarousel(props){
    const [carouselHeight, setCarouselHeight] = React.useState('50vh');
    const [navButtonsAlwaysVisible, setNavButtonsAlwaysVisible] = React.useState(false)
    const [stopAutoPlayOnHover, setStopAutoPlayOnHover] = React.useState(false)

    React.useEffect(() => {
        setCarouselHeight(undefined)
        setNavButtonsAlwaysVisible(true)
        setStopAutoPlayOnHover(true)
    }, [])
    var items = [
        {
            index: 0,
            src: "/RedCanyon2.jpg",
            alt: "Red Canyon"
        },
        {
            index: 1,
            src: "/engagement.jpg",
            alt: "Engagement In Hot Spring"
        },
        {
            index: 2,
            src: "/dogs.jpeg",
            alt: "Hank and Boomer at The Bus"
        },
        {
            index: 3,
            src: "/IMG_0667.jpeg",
            alt: "Lisa and Allen Spring 2019"
        },
        {
            index: 4,
            src: "/IMG_0962.jpeg",
            alt: "Lisa and Allen backcountry skiing"
        },
        {
            index: 5,
            src: "/IMG_1849.jpeg",
            alt: "Lisa, Allen, Hank, and Boomer backpacking"
        },
        {
            index: 6,
            src: "/IMG_2213.jpeg",
            alt: "Hank and Boomer at Johnny Falls"
        },
        {
            index: 7,
            src: "/IMG_3372.jpeg",
            alt: "Lisa and Allen hunting"
        },
        {
            index: 8,
            src: "/IMG_3450.jpeg",
            alt: "Lisa and Allen riding horses"
        },
        {
            index: 9,
            src: "/JTK_7973.jpg",
            alt: "Lisa and Allen engagement photo shoot"
        }
    ]

    return (
        <Carousel
            height={carouselHeight}
            navButtonsAlwaysVisible={navButtonsAlwaysVisible}
            autoPlay={true}
            stopAutoPlayOnHover={stopAutoPlayOnHover}
        >
            {
                items.map( (item, i) => <Item key={i} item={item} /> )
            }
        </Carousel>
    )
}

function Item(props)
{
    return (
        <Box>
            <div
                sx={{height:'min-content'}}
                // id={`image-${props.item.index}`}
            >
                <img
                    className='photoWrapper'
                    src={props.item.src}
                    alt={props.item.alt}
                />
            </div>
        </Box>

    )
}

export default ImageCarousel